.modal_style {
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: 9999;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.4);
}

.card_modal {
  width: 600px;
  background: #fff;
  padding: 30px;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  position: relative;
}

.form_card {
  width: 100%;
}

.modal_cross {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 10px;
  background: #fefbf6;
  font-size: 14px !important;
  border-radius: 10px;
}

.card_modal .form-input {
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 8px;
  padding: 12px 14px;
  outline: none;
  transition: border-color .2s, box-shadow .2s;
}

.card_modal .form-input:focus {
  border-color: rgba(1, 93, 224, .6);
  box-shadow: 0 0 0 4px rgba(1, 93, 224, .08);
}

.card_modal .rounded-8 {
  border-radius: 8px;
}

.card_modal .shadow-2 {
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.12);
}

.card_modal button.button_submit {
  width: 100%;
  padding: 12px;
  border-radius: 8px;
  background-color: #EFA852;
  color: #fff;
  border: none;
  cursor: pointer;
  transition: background-color .2s;
  margin-bottom: 10px;
  margin-top: 20px;
}

.alert_form {
  width: 100%;
  background: #fff3df;
