/* Card shell */
.tpb-card {
  background: #fff;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 10px 24px rgba(0, 0, 0, .0);
  height: 100%;
  display: flex;
  flex-direction: column;
  transition: transform .15s ease, box-shadow .15s ease;
}

.tpb-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 16px 32px rgba(0, 0, 0, .01);
}

.tpb-card--alt {
  background: #f7f7f8;
}

/* Image area  16:9 */
.tpb-card__image {
  position: relative;
}

.tpb-card--compact .tpb-card__image {
  aspect-ratio: auto;
  /* cancel the old ratio */
  height: clamp(160px, 20vw, 220px);
  overflow: hidden;
}

.tpb-card__mediaLink,
.tpb-card__mediaSwiper,
.tpb-card__mediaSwiper .swiper,
.tpb-card__mediaSwiper .swiper-wrapper,
.tpb-card__mediaSwiper .swiper-slide {
  height: 100%;
}

.tpb-card__mediaSwiper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

/* Badge */
.tpb-card__badge {
  position: absolute;
  left: 12px;
  top: 12px;
}

.tpb-badge {
  padding: 4px 10px;
  font-size: 12px;
  font-weight: 600;
  border-radius: 999px;
  box-shadow: 0 4px 12px rgba(0, 0, 0, .15);
  background: #fff;
  color: #111;
  text-transform: uppercase;
}

.tpb-badge--dark {
  background: #111;
  color: #fff;
}

.tpb-badge--blue {
  background: #2b67f6;
  color: #fff;
}

.tpb-badge--yellow {
  background: #ffd84d;
  color: #111;
}

/* Content */
.tpb-card__content {
  padding: 14px 16px 16px;
  display: flex;
  flex-direction: column;
  gap: 6px;
  flex: 1;
}

.tpb-card__meta {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 13px;
  color: #6b7280;
}

.tpb-card__type {
  font-size: 14px;
  color: #6b7280;
}

.tpb-card__title {
  color: #0f172a;
  font-size: 15px;
  font-weight: 600;
  line-height: 1.35;
  margin: 2px 0 0;
  text-align: left;
}

.tpb-card__title a {
  color: inherit;
  text-decoration: none;
}

.tpb-card__title a:hover {
  text-decoration: none;
}

.tpb-card__duration {
  color: #6b7280;
  font-size: 14px;
  text-align: left;
}

/* Bottom row */
.tpb-card__bottom {
  margin-top: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
}

.tpb-card__price {
  font-size: 14px;
  color: #6b7280;
}

.tpb-card__price strong {
  font-size: 18px;
  color: #111827;
  font-weight: 800;
}

/* CTA */
.tpb-card__cta {
  align-items: center;
  background: #f59e0b;
  border-radius: 8px;
  color: #fff;
  display: inline-flex;
  font-weight: 600;
  justify-content: center;
  min-width: 120px;
  padding: 6px 10px;
  text-decoration: none;
  font-size: 15px;
}

.tpb-card__cta:hover {
  background: #e0920b;
  color: #fff;
}

/* Scope to the small media swiper inside the card */
.tpb-card__mediaSwiper {
  /* white icons/dots, smaller arrow glyphs */
  --swiper-navigation-color: #fff;
  --swiper-pagination-color: #fff;
  --swiper-theme-color: #fff;
  /* fallback for some builds */
  --swiper-navigation-size: 16px;
  /* arrow icon size */
  position: relative;
  z-index: 1;
}

/* Pagination pill (centered at bottom) */
.tpb-card__mediaSwiper .swiper-pagination {
  bottom: 8px !important;
  left: 50% !important;
  transform: translateX(-50%);
  width: auto !important;
  padding: 0px 10px;
  background: rgba(0, 0, 0, 0.45);
  border-radius: 9999px;
  -webkit-backdrop-filter: saturate(110%) blur(1px);
  backdrop-filter: saturate(110%) blur(1px);
  height: 25px;
  line-height: 22px;
}

/* Small white dots */
.tpb-card__mediaSwiper .swiper-pagination-bullet {
  width: 6px;
  height: 6px;
  margin: 0 3px !important;
  background: rgba(255, 255, 255, 0.6);
  opacity: 1;
  /* keep visible */
}

.tpb-card__mediaSwiper .swiper-pagination-bullet-active {
  background: #fff;
}

/* Arrow buttons: small circles with same bg */
.tpb-card__mediaSwiper .swiper-button-prev,
.tpb-card__mediaSwiper .swiper-button-next {
  width: 28px;
  height: 28px;
  border-radius: 9999px;
  background: rgba(0, 0, 0, 0.45);
  /* same transparent black */
  color: #fff;
  /* icon color (fallback) */
}

.tpb-card__mediaSwiper .swiper-button-prev {
  left: 8px;
}

.tpb-card__mediaSwiper .swiper-button-next {
  right: 8px;
}

/* Hover (optional) */
.tpb-card__mediaSwiper .swiper-button-prev:hover,
.tpb-card__mediaSwiper .swiper-button-next:hover,
.tpb-card__mediaSwiper .swiper-pagination:hover {
  background: rgba(0, 0, 0, 0.65);
}

