/* Dropdown */
.user-menu {
  position: absolute;
  right: 0;
  top: calc(100% + 10px);
  min-width: 260px;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 12px 28px rgba(0, 0, 0, .16);
  border: 1px solid rgba(0, 0, 0, .06);
  opacity: 0;
  transform: translateY(-6px);
  pointer-events: none;
  transition: opacity .18s ease, transform .18s ease;
  z-index: 1000;
}

.user-menu.open {
  opacity: 1;
  transform: translateY(0);
  pointer-events: auto;
}

/* Caret tip */
.user-menu::before {
  content: "";
  position: absolute;
  top: -6px;
  right: 22px;
  width: 12px;
  height: 12px;
  background: #fff;
  transform: rotate(45deg);
  border-left: 1px solid rgba(0, 0, 0, .06);
  border-top: 1px solid rgba(0, 0, 0, .06);
}

/* Menu items */
.menu-item {
